/* color scheme: Gorgeous Contrast

#61892F : Other Text
#86C232 : Text Bold
#222629 : background
#474B4F : Text greyed
#6B6E70 : Others


*/

.font-bold {
  font-family: "LibreBaskervilleBold";
}

.font-regular {
  font-family: "LibreBaskervilleRegular";
}

.font-italic {
  font-family: "LibreBaskervilleItalic";
}

body, html {
  height: 100%;
  background-color: #222629;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #e8e8e7;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Navbar {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #050505;
  font-size: calc(8px + 2vmin);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.Navbar li {
  float: left;
}

.Navbar li a {
  display: block;
  color: #86C232;
  text-align: center;
  padding: 20px 16px;
  text-decoration: none;
}

.Navbar li a:hover {
  color: #ffffff;
}

.Navbar .active {
  background-color: #61892F;
}

.Brand {
  color: Black;
}

.bg {
  background-image: url("images/DJI_0812.jpg");
  height: 500vh;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}